import uniPopup from '@/components/uni-popup/uni-popup.vue';
import {mapState,mapMutations} from 'vuex';
export default{
	components:{
		uniPopup
	},
	data(){
		return {
			//影院座位的二维数组，0为未选择位置，1为已选择位置，-1为非座位
			seatArray:[],
			//影院座位的行数
			seatRow:10,
			//影院座位的列数
			seatCol:20,
			//影院座位的尺寸
			seatSize:35,
			//是否已经选择座位的标志
			alreadySelected:false,
			//当前选中的行
			currentRow:0,
			//当前选中的列
			currentCol:0,
			//确定选中
			confirmed:false,
		}
	},
	computed:{
		
	},
	methods:{
		...mapMutations(['selected','selectResult']),
		//初始化座位数组
		initSearArray(){
			let seatArray = new Array(this.seatRow);
			for(let i=0;i<this.seatRow;i++){
				seatArray[i] = new Array(this.seatCol);
				for(let j=0;j<this.seatCol;j++){
					seatArray[i][j] = 0;
				}
			}
			this.seatArray = seatArray;
			this.initNonSeatPlace()
		},
		//初始化非座位的地方
		initNonSeatPlace(){
			
		},
		//处理座位的选中
		handleSelect(row,col){
			this.seatReset();
			this.alreadySelected = true;
			let newArray = this.seatArray;
			if(newArray[row][col]===0){
				newArray[row][col] = 1
			}else{
				newArray[row][col] = 0
			}
			this.seatArray = newArray.slice();
			this.currentRow = row+1;
			this.currentCol = col+1;
		},
		//座位重置
		seatReset(){
			let newArray = this.seatArray;
			for(let i=0;i<this.seatRow;i++){
				for(let j=0;j<this.seatCol;j++){
					newArray[i][j] = 0
				}
			}
			this.alreadySelected = false;
			this.seatArray = newArray.slice();
		},
		//选择座位
		chooseSeat(){
			this.confirmed = true;
			this.$refs.tip.open();
		},
		//取消选择
		cancel() {
			this.$refs.tip.close();
		},
		//确认选择
		confirm(){
			this.$refs.tip.close();
			this.selected(true);
			this.selectResult(this.seatArray);
			wx.navigateBack();
		}
	},
	onReady() {
		this.initSearArray()
	}
}
